@import '../components/common/sass/variables.scss';
@import '../components/common/sass/mixins.scss';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.main {
    height: 100vh;
    margin-bottom: 150px;
    @include flexBox(nowrap, row, center, center);

    .main_container {
        height: 100%;
        width: 80%;
        @include flexBox(wrap, row, space-around, center);

        .greeting {
            width: 400px;
            font-family: 'Josefin Sans', sans-serif;
            color: $secondary_color;
            line-height: 1.5;

            .greeting_span {
                font-size: $primaryTitle;
                font-weight: 300;

                @media screen and (max-width: 520px) {
                    font-size: $secondaryTitle;
                    font-weight: 400;
                }
            }

            h1 {
                font-size: 60px;
                font-weight: 700;

                .name {
                    color: $primary_color;
                }

                @media screen and (max-width: 520px) {
                    font-size: 40px;
                    font-weight: 700;
                }
            }

            .position_span {
                font-size: $secondaryTitle;
                font-weight: 400;

                @media screen and (max-width: 520px) {
                    font-size: $normalTextSize;
                    font-weight: 400;
                }
            }

            .btn_container {
                height: 75px;
                text-align: center;
                @include flexBox(nowrap, row, flex-start, center);

                .projects_btn {
                    text-decoration: none;
                    margin-right: 20px;
                    width: 145px;
                    height: 50px;
                    display: inline-block;
                    padding: 14px 10px;
                    cursor: pointer;
                    font-size: $smallTextSize;
                    background-color: $primary_color;
                    color: $secondary_color;
                    border: $secondary_color 2px solid;

                    &:hover {
                        transition: ease-in-out all 0.55s;
                        border: $primary_color 2px solid;
                        background-color: $secondary_color;
                        color: $primary_color;
                    }

                    @media screen and (max-width: 520px) {
                        width: 120px;
                        height: 45px;
                        padding: 13px 10px;
                        margin-right: 10px;
                        font-size: $xsmallTextSize;
                    }
                }

                .active {
                    color: $secondary_color;
                }

                .projects_btn:not(:hover) {
                    transition: ease-in-out all 0.55s;
                }

                .contact_btn {
                    text-decoration: none;
                    width: 145px;
                    height: 50px;
                    display: inline-block;
                    padding: 14px 10px;
                    cursor: pointer;
                    font-size: $smallTextSize;
                    border: $primary_color 2px solid;
                    background-color: $secondary_color;
                    color: $primary_color;

                    &:hover {
                        transition: ease-in-out all 0.55s;
                        background-color: $primary_color;
                        color: $secondary_color;
                        border: $secondary_color 2px solid;
                    }

                    @media screen and (max-width: 520px) {
                        width: 120px;
                        height: 45px;
                        padding: 13px 10px;
                        font-size: $xsmallTextSize;
                    }
                }

                .contact_btn:not(:hover) {
                    transition: ease-in-out all 0.55s;
                }

            }

            @media screen and (max-width: 360px) {
                width: 250x;
            }

            @media screen and (max-width: 520px) {
                width: 250px;
            }

            @media screen and (max-width: 1210px) {
                margin: 5px;
                text-align: center;
                @include flexBox(nowrap, column, center, center);
            }
        }

        .main_img {
            width: 550px;
            height: 550px;
            border: 8px solid $primary_color;
            border-radius: 50%;
            background-color: $secondary_color;
            background-size: 115%;
            background-position: center;
            background-repeat: no-repeat;
            position: relative;
            box-shadow: $primary_color 0px 2px 20px;

            @media screen and (max-width: 1210px) {
                display: none;
            }
        }

        .secondary_img {
            display: none;
            width: 450px;
            height: 450px;
            border: 5px solid $primary_color;
            box-shadow: $primary_color 0px 2px 15px;
            border-radius: 50%;
            background-color: $secondary_color;
            background-size: 85%;
            background-position: center;
            background-repeat: no-repeat;
            position: relative;

            @media screen and (max-width: 360px) {
                width: 250px;
                height: 250px;
            }

            @media screen and (max-width: 520px) {
                width: 300px;
                height: 300px;
            }

            @media screen and (max-width: 1210px) {
                display: block;
                margin: 5px;
            }

        }

        @media screen and (max-width: 1210px) {
            width: 100%;
            text-align: center;
            @include flexBox(nowrap, column, space-evenly, center);
        }
    }

    @media only screen and (device-width: 740px) and (orientation: landscape) {
        /* css rules */
    }

    @media screen and (max-width: 820px) {
        margin-bottom: 100px;
    }

    @media screen and (max-width: 520px) {
        height: 100vh;
        margin-bottom: 50px;
    }
}